/**
 * CloudinaryÁîªÂÉèÊúÄÈÅ©Âåñ„É¶„Éº„ÉÜ„Ç£„É™„ÉÜ„Ç£
 */

interface ImageOptimizationOptions {
  width?: number;
  height?: number;
  quality?: string | number;
  format?: 'auto' | 'webp' | 'avif' | 'jpg' | 'png';
  crop?: 'fill' | 'fit' | 'limit' | 'scale' | 'pad';
  dpr?: 'auto' | number;
  blur?: number;
}

/**
 * Cloudinary URL„ÇíÈ´òÂ∫¶„Å´ÊúÄÈÅ©Âåñ
 */
export const optimizeCloudinaryUrlAdvanced = (
  url: string,
  options: ImageOptimizationOptions = {}
): string => {
  if (!url || !url.includes('cloudinary.com')) {
    return url;
  }

  const {
    width,
    height,
    quality = 'auto',
    format = 'auto',
    crop = 'limit',
    dpr = 'auto',
    blur
  } = options;

  // „Éë„É©„É°„Éº„Çø„ÇíÊßãÁØâ
  const params: string[] = [];

  // „Éï„Ç©„Éº„Éû„ÉÉ„ÉàÔºàWebP/AVIFËá™ÂãïÈÅ∏ÊäûÔºâ
  params.push(`f_${format}`);

  // ÂìÅË≥™
  params.push(`q_${quality}`);

  // „Çµ„Ç§„Ç∫
  if (width) params.push(`w_${width}`);
  if (height) params.push(`h_${height}`);

  // „ÇØ„É≠„ÉÉ„Éó„É¢„Éº„Éâ
  params.push(`c_${crop}`);

  // „Éá„Éê„Ç§„Çπ„Éî„ÇØ„Çª„É´ÊØî
  params.push(`dpr_${dpr}`);

  // „Åº„Åã„ÅóÂäπÊûúÔºà„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„ÉºÁî®Ôºâ
  if (blur) params.push(`e_blur:${blur}`);

  // Êó¢Â≠ò„ÅÆupload/‰ª•Èôç„ÅÆ„Éë„É©„É°„Éº„Çø„ÇíÂâäÈô§
  const baseUrl = url.split('/upload/')[0];
  const imagePath = url.split('/upload/').pop()?.replace(/^[^/]*\//, '') || '';

  return `${baseUrl}/upload/${params.join(',')}/${imagePath}`;
};

/**
 * ‰ΩéÂìÅË≥™„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„ÉºÁîªÂÉèURLÁîüÊàêÔºàLQIPÔºâ
 */
export const generateLQIP = (url: string): string => {
  if (url.includes('cloudinary.com')) {
    return optimizeCloudinaryUrlAdvanced(url, {
      width: 40,
      quality: 30,
      blur: 1000,
      format: 'jpg'
    });
  }
  return url;
};

/**
 * „É¨„Çπ„Éù„É≥„Ç∑„ÉñÁîªÂÉè„ÅÆsrcsetÁîüÊàê
 */
export const generateSrcSet = (url: string, sizes: number[] = [320, 640, 768, 1024, 1280, 1536, 1920]): string => {
  if (!url) return '';

  return sizes
    .map(size => {
      const optimizedUrl = optimizeAnyImageUrl(url, size, Math.round(size * 0.75));
      return `${optimizedUrl} ${size}w`;
    })
    .join(', ');
};

// Êó¢Â≠ò„ÅÆÈñ¢Êï∞ÔºàÂæåÊñπ‰∫íÊèõÊÄß„ÅÆ„Åü„ÇÅÁ∂≠ÊåÅÔºâ
export const optimizeCloudinaryUrl = (url: string, width: number, height: number): string => {
  return optimizeCloudinaryUrlAdvanced(url, { width, height });
};

export const optimizeAnyImageUrl = (url: string, width: number, height: number): string => {
  if (!url) return url;

  // Cloudinary URL„ÅÆÂ†¥Âêà„ÅØÂ∞ÇÁî®„ÅÆÊúÄÈÅ©Âåñ„ÇíÈÅ©Áî®
  if (url.includes('cloudinary.com')) {
    return optimizeCloudinaryUrl(url, width, height);
  }

  // Unsplash URL„ÅÆÂ†¥Âêà„ÅØÊó¢Â≠ò„ÅÆ„Éë„É©„É°„Éº„Çø„ÇíÊõ¥Êñ∞
  if (url.includes('images.unsplash.com')) {
    // Êó¢Â≠ò„ÅÆ„Éë„É©„É°„Éº„Çø„ÇíÂâäÈô§„Åó„Å¶Êñ∞„Åó„ÅÑ„Çµ„Ç§„Ç∫„ÇíÈÅ©Áî®
    const baseUrl = url.split('?')[0];
    return `${baseUrl}?w=${width}&h=${height}&fit=crop&auto=format,compress`;
  }

  // „Åù„ÅÆ‰ªñ„ÅÆURL„ÅØ„Åù„ÅÆ„Åæ„ÅæËøî„Åô
  return url;
};

/**
 * ÁîªÂÉè„Éï„Ç©„Éº„Éû„ÉÉ„Éà„ÅÆ„Çµ„Éù„Éº„Éà„ÉÅ„Çß„ÉÉ„ÇØ
 */
export const supportsWebP = (): boolean => {
  if (typeof window === 'undefined') return false;

  const canvas = document.createElement('canvas');
  canvas.width = canvas.height = 1;
  return canvas.toDataURL('image/webp').indexOf('image/webp') === 5;
};

export const supportsAVIF = (): boolean => {
  if (typeof window === 'undefined') return false;

  const canvas = document.createElement('canvas');
  canvas.width = canvas.height = 1;
  return canvas.toDataURL('image/avif').indexOf('image/avif') === 5;
};