'use client';

import React, { useState, useEffect, useRef } from 'react';

interface OptimizedImageProps {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  className?: string;
  sizes?: string;
  priority?: boolean; // true„ÅÆÂ†¥Âêà„ÅØÈÅÖÂª∂Ë™≠„ÅøËæº„Åø„Çí„Åó„Å™„ÅÑ
  objectFit?: 'cover' | 'contain' | 'fill' | 'none' | 'scale-down';
  onClick?: () => void;
}

/**
 * Cloudinary URL„ÇíÊúÄÈÅ©Âåñ„Åô„Çã
 * @param url - ÂÖÉ„ÅÆURL
 * @param width - ÂπÖ
 * @param quality - ÂìÅË≥™Ôºà„Éá„Éï„Ç©„É´„Éà: autoÔºâ
 */
const optimizeCloudinaryUrl = (url: string, width: number, quality: string = 'auto'): string => {
  if (!url || !url.includes('cloudinary.com')) {
    return url;
  }

  // Êó¢Â≠ò„ÅÆupload/‰ª•Èôç„ÅÆ„Éë„É©„É°„Éº„Çø„ÇíÂâäÈô§
  const baseUrl = url.split('/upload/')[0];
  const imagePath = url.split('/upload/').pop()?.replace(/^[^/]*\//, '') || '';

  // Cloudinary„ÅÆÂ§âÊèõ„Éë„É©„É°„Éº„Çø„ÇíËøΩÂä†
  // f_auto: ÊúÄÈÅ©„Å™„Éï„Ç©„Éº„Éû„ÉÉ„ÉàÔºàWebP/AVIFÔºâ„ÇíËá™ÂãïÈÅ∏Êäû
  // q_auto: ÊúÄÈÅ©„Å™ÂìÅË≥™„ÇíËá™ÂãïÈÅ∏Êäû
  // dpr_auto: „Éá„Éê„Ç§„Çπ„Éî„ÇØ„Çª„É´ÊØî„ÇíËá™ÂãïÊ§úÂá∫
  // c_limit: „Ç¢„Çπ„Éö„ÇØ„ÉàÊØî„ÇíÁ∂≠ÊåÅ„Åó„Å™„Åå„Çâ„Çµ„Ç§„Ç∫Âà∂Èôê
  return `${baseUrl}/upload/f_auto,q_${quality},w_${width},c_limit,dpr_auto/${imagePath}`;
};

/**
 * Unsplash URL„ÇíÊúÄÈÅ©Âåñ„Åô„Çã
 */
const optimizeUnsplashUrl = (url: string, width: number): string => {
  if (!url || !url.includes('images.unsplash.com')) {
    return url;
  }

  const baseUrl = url.split('?')[0];
  return `${baseUrl}?w=${width}&fit=crop&auto=format,compress`;
};

/**
 * ‰ªªÊÑè„ÅÆÁîªÂÉèURL„ÇíÊúÄÈÅ©Âåñ„Åô„Çã
 */
const optimizeImageUrl = (url: string, width: number): string => {
  if (!url) return url;

  if (url.includes('cloudinary.com')) {
    return optimizeCloudinaryUrl(url, width);
  }

  if (url.includes('images.unsplash.com')) {
    return optimizeUnsplashUrl(url, width);
  }

  return url;
};

/**
 * ÊúÄÈÅ©Âåñ„Åï„Çå„ÅüÁîªÂÉè„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà
 * - „É¨„Çπ„Éù„É≥„Ç∑„ÉñÁîªÂÉèÔºàsrcsetÔºâÂØæÂøú
 * - ÈÅÖÂª∂Ë™≠„ÅøËæº„ÅøÔºàIntersection ObserverÔºâ
 * - WebP/AVIFËá™ÂãïÂ§âÊèõÔºàCloudinaryÔºâ
 * - „Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„ÉºË°®Á§∫
 */
const OptimizedImage: React.FC<OptimizedImageProps> = ({
  src,
  alt,
  width,
  height,
  className = '',
  sizes = '100vw',
  priority = false,
  objectFit = 'cover',
  onClick
}) => {
  const [isInView, setIsInView] = useState(priority);
  const [isLoaded, setIsLoaded] = useState(false);
  const [hasError, setHasError] = useState(false);
  const imgRef = useRef<HTMLImageElement>(null);

  // „É¨„Çπ„Éù„É≥„Ç∑„ÉñÁîªÂÉè„ÅÆ„Éñ„É¨„Éº„ÇØ„Éù„Ç§„É≥„Éà
  const breakpoints = [320, 640, 768, 1024, 1280, 1536, 1920];

  // srcset„ÇíÁîüÊàê
  const generateSrcSet = (): string => {
    if (!src) return '';

    return breakpoints
      .map(bp => `${optimizeImageUrl(src, bp)} ${bp}w`)
      .join(', ');
  };

  // Intersection Observer„ÅßÈÅÖÂª∂Ë™≠„ÅøËæº„Åø
  useEffect(() => {
    if (priority || !imgRef.current) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            setIsInView(true);
            observer.disconnect();
          }
        });
      },
      {
        rootMargin: '50px' // 50pxÊâãÂâç„Åã„ÇâË™≠„ÅøËæº„ÅøÈñãÂßã
      }
    );

    observer.observe(imgRef.current);

    return () => observer.disconnect();
  }, [priority]);

  // „Ç®„É©„ÉºÊôÇ„ÅÆ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÁîªÂÉè
  const handleError = () => {
    setHasError(true);
    setIsLoaded(true);
  };

  // „Éá„Éï„Ç©„É´„Éà„ÅÆ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÁîªÂÉè
  const fallbackSrc = 'https://images.unsplash.com/photo-1515377905703-c4788e51af15?w=400&h=300&fit=crop&auto=format';

  // ÁîªÂÉè„ÅÆ„Çπ„Çø„Ç§„É´
  const imgStyle: React.CSSProperties = {
    objectFit,
    transition: 'opacity 0.3s ease-in-out',
    opacity: isLoaded ? 1 : 0,
    width: width ? `${width}px` : '100%',
    height: height ? `${height}px` : 'auto'
  };

  // „Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„Éº„ÅÆ„Çπ„Çø„Ç§„É´
  const placeholderStyle: React.CSSProperties = {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: '#f3f4f6',
    display: isLoaded ? 'none' : 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  };

  // ÁîªÂÉèURL„ÇíÊ±∫ÂÆö
  const imageSrc = hasError ? fallbackSrc : src;
  const optimizedSrc = optimizeImageUrl(imageSrc, width || 800);

  return (
    <div
      className={`relative ${className}`}
      style={{ width: width ? `${width}px` : '100%', height: height ? `${height}px` : 'auto' }}
      onClick={onClick}
    >
      {/* „Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„Éº */}
      <div style={placeholderStyle}>
        <div className="animate-pulse">
          <svg className="w-12 h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
          </svg>
        </div>
      </div>

      {/* „É°„Ç§„É≥ÁîªÂÉè */}
      {isInView && (
        <picture>
          {/* WebPÁâàÔºàCloudinary„ÅÆf_auto„ÅßËá™ÂãïÂØæÂøúÔºâ */}
          <source
            type="image/webp"
            srcSet={generateSrcSet()}
            sizes={sizes}
          />

          {/* „Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØÁîªÂÉè */}
          <img
            ref={imgRef}
            src={optimizedSrc}
            srcSet={generateSrcSet()}
            sizes={sizes}
            alt={alt}
            loading={priority ? 'eager' : 'lazy'}
            decoding={priority ? 'sync' : 'async'}
            onLoad={() => setIsLoaded(true)}
            onError={handleError}
            style={imgStyle}
            className={className}
          />
        </picture>
      )}

      {/* ÈùûË°®Á§∫„ÅÆÂ†¥Âêà„ÅÆ„Éó„É¨„Éº„Çπ„Éõ„É´„ÉÄ„Éº */}
      {!isInView && (
        <div ref={imgRef} style={{ width: '100%', height: '100%' }} />
      )}
    </div>
  );
};

export default OptimizedImage;